@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');
html,
body {
	background: black;
	color: #00b800;
	margin: 0;
	padding: 0;
	padding-top: 10px;
	font-family: 'Press Start 2P';
	text-align: center;
}

body {
	font-weight: 400;
}

button {
	font-family: 'Press Start 2P';
	display: block;
	margin: 0 auto;
	border: none;
	background: #a80020;
	background: #00b800;
	padding: 15px 30px;
	text-transform: uppercase;
}

h1 {
	font-size: 20px;
	text-align: center;
	padding: 20px;
}

textarea {
	font-family: 'Press Start 2P';
	border: 2px solid #00b800;
	width: 50%;
	margin: 0 auto;
	outline: none;
	height: 200px;
	background: #00b800;
	padding: 10px;
}

textarea:disabled {
	background-color: #a5a2a2;
}

.container {
	padding: 50px;
}

p {
	font-size: 12px;
}
